import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    is_authenticated: false,
    home_page: undefined
  },
  getters: {
    IS_AUTH: state => state.is_authenticated,
    HOME_PAGE: state => state.home_page
  },
  mutations: {
    LOGIN: (state) => {
      state.is_authenticated = true
    },
    LOGOUT: (state) => {
      state.is_authenticated = false
      state.home_page = undefined
    },
    SET_HOME_PAGE: (state, pageId) => {
      state.home_page = pageId
    }
  },
  actions: {
    LOGIN (context) {
      context.commit('LOGIN')
    },
    LOGOUT (context) {
      context.commit('LOGOUT')
    },
    SET_HOME_PAGE (context) {
      axios.get('/api/v1/auth/get_info')
        .then(response => {
          let pageId = response.data.page_id
          context.commit('SET_HOME_PAGE', pageId)
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
})



