<template>
  <div>
    <header
      class="navbar"
      :class="{ 'navbar--hidden': !showNavbar }">
      <v-container class="nav-wrapper">
        <v-row align="center">
          <v-spacer></v-spacer>
          <div class="nav-option">
            <login v-if="!isAuth">
              <v-btn text small color="primary" >Войти</v-btn>
            </login>
          </div>
          <div class="nav-option">
            <create-card v-if="isAuth" :editing="false">
              <v-hover v-slot:default="{ hover }">
                <v-btn :color="hover ? 'green' : 'green lighten-3'" text small title="Добавить карточку">
                  <v-icon>mdi-card-plus</v-icon> Добавить
                </v-btn>
              </v-hover>
            </create-card>
          </div>
          <div class="nav-option">
            <v-menu open-on-hover offset-y bottom v-if="isAuth">
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" color="grey">mdi-account</v-icon>
              </template>

              <v-list>
                <v-list-item>
                  <router-link :to="{name: 'page', params: {page: homePageId}}">
                    <v-btn text small color="grey darken-1" title="Моя коллекция">
                      <v-icon>mdi-home</v-icon> Коллекция
                    </v-btn>
                  </router-link>
                </v-list-item>
                <v-list-item>
                  <logout>
                    <v-btn text small color="grey darken-1"  title="Выйти">
                      <v-icon>mdi-account-arrow-right-outline</v-icon>Выйти
                    </v-btn>
                  </logout>
                </v-list-item>
              </v-list>
            </v-menu>
          </div>
        </v-row>
      </v-container>
    </header>
  </div>

</template>

<script>
  import Login from './Login'
  import Logout from './Logout'
  import CreateCard from './CreateCard'
  import { mapGetters } from 'vuex'

  export default {
    name: 'Header',
    components: {
      Login,
      Logout,
      CreateCard
    },
    data () {
      return {
        showNavbar: true,
        lastScrollPosition: 0
      }
    },
    computed: {
      ...mapGetters({
        isAuth: 'IS_AUTH',
        homePageId: 'HOME_PAGE'
      })
    },
    mounted () {
      window.addEventListener('scroll', this.onScroll)
    },
    beforeDestroy () {
      window.removeEventListener('scroll', this.onScroll)
    },
    methods: {
      onScroll () {
        const currentScrollPosition = window.pageYOffset || document.documentElement.scrollTop
        if (currentScrollPosition < 0) {
          return
        }
        if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 60) {
          return
        }
        this.showNavbar = currentScrollPosition < this.lastScrollPosition
        this.lastScrollPosition = currentScrollPosition
      }
    }
  }
</script>

<style>
  .navbar {
    height: 3rem;
    width: 100%;
    background: #fafafa;
    position: fixed;
    transform: translate3d(0, 0, 0);
    transition: 0.1s all ease-out;
    z-index: 10;
  }

  .navbar.navbar--hidden {
    transform: translate3d(0, -100%, 0);
  }

  .v-icon {
    margin-right: 0.3rem;
  }

  .nav-wrapper {
    float: right;
    padding-right: 3rem;
  }
</style>



