<template>
  <div>
    <div @click="openDialog">
      <slot></slot>
    </div>
    <v-dialog
      width="unset"
      v-model="createCardDialog"
    >
      <v-card>
        <div class="create-card">
          <h1 class="headline">Создание контента</h1>
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="card-form">
            <v-layout wrap>
              <v-flex xs12 md12 lg6 class="pa-0">
                <!-- Title -->
                <v-text-field
                  v-model="cardTitle"
                  label="Название"
                  :rules="[v => !!v || 'Нужно дать имя']"
                  required>
                  </v-text-field>
                <!-- Image -->
                <div class="image-input-group">
                  <v-text-field v-model="cardURL" label="URL картинки"></v-text-field>
                </div>
                <!-- Author -->
                <v-combobox
                  v-model="cardAuthor.value"
                  :items="cardAuthor.variants"
                  item-text="name"
                  item-value="id"
                  :search-input.sync="cardAuthor.search"
                  hide-selected
                  label="Автор"
                  persistent-hint
                  small-chips
                  >
                  <template v-if="cardAuthor.noData" v-slot:no-data>
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>
                          "<strong>{{ cardAuthor.search }}</strong>" нету. Нажми <kbd>enter</kbd> для добавления.
                        </v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </template>
                </v-combobox>
                <!-- Type -->
                <v-combobox
                  v-model="cardType.value"
                  :items="cardType.variants"
                  item-text="name"
                  item-value="id"
                  :search-input.sync="cardType.search"
                  hide-selected
                  label="Тип"
                  persistent-hint
                  small-chips
                  :rules="[v => !!v || 'Нужно выбрать тип']"
                  required
                >
                  <template v-if="cardType.noData" v-slot:no-data>
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>
                          "<strong>{{ cardType.search }}</strong>" нету. Нажми <kbd>enter</kbd> для добавления.
                        </v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </template>
                </v-combobox>
                <!-- Tags -->
                <v-combobox
                  v-model="cardTags.value"
                  :items="cardTags.variants"
                  :search-input.sync="cardTags.search"
                  item-text="name"
                  item-value="id"
                  hide-selected
                  label="Тэги"
                  persistent-hint
                  small-chips
                  multiple
                  deletable-chips
                >
                  <template v-if="cardTags.noData" v-slot:no-data>
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>
                          "<strong>{{ cardTags.search }}</strong>" нету. Нажми <kbd>enter</kbd> для добавления.
                        </v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </template>
                </v-combobox>
                <!-- Rating -->
                <v-autocomplete
                  v-model="cardRate.value"
                  :items="cardRate.variants"
                  item-text="name"
                  item-value="rate"
                  :search-input.sync="cardRate.search"
                  label="Степень годноты"
                  small-chips
                  :rules="[v => !!v || 'Нужно дать оценку']"
                  required
                >
                </v-autocomplete>
              </v-flex>
              <v-flex xs12 md12 lg6 class="pa-0">
                <div class="card-wrapper">
                  <card
                    :editing="true"
                    :info="resultCard"
                    :tagsFiltered="[]">
                  </card>
                </div>
              </v-flex>
            </v-layout>
            <v-row justify="end" class="mt-3">
              <v-btn :disabled="!valid" :color="color" @click="post" dense>
                <v-progress-circular
                  v-if="loading"
                  indeterminate
                  color="white darken-1"
                  class="mr-2"
                  size="18"
                  width="2"
                ></v-progress-circular>
                <v-icon left v-if="!loading && success == null">mdi-content-save-outline</v-icon>
                <v-icon left v-if="!loading && success == true">mdi-check</v-icon>
                <v-icon left v-if="!loading && success == false">mdi-alert-circle-outline</v-icon>
                Сохранить
              </v-btn>
            </v-row>
          </v-form>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from 'axios'

  const urlPost = '/api/v1/card/create/'
  const urlFilters = '/api/v1/filters/'

  export default {
    name: 'CreateCard',
    props: ['cardInfo', 'editing'],
    data () {
      return {
        valid: false,
        createCardDialog: false,
        loading: false,
        success: null,
        color: 'success',
        cardId: null,
        cardTitle: undefined,
        cardURL: null,
        cardAuthor: {
          value: '',
          variants: [],
          search: null,
          noData: true
        },
        cardType: {
          value: null,
          variants: [],
          search: null,
          noData: true
        },
        cardTags: {
          value: null,
          variants: [],
          search: null,
          noData: true
        },
        cardRate: {
          value: null,
          variants: [],
          search: null,
          noData: true
        },
        resultCard: {
          title: 'Название',
          author: {name: 'автор', id: 0},
          color: '#818181',
          id: 0,
          imgur_url: 'https://i.imgur.com/ZRguE01.png',
          rating: {name: 'Степень годноты', id: 0},
          tags: [{name: 'Тэги', id: 0}],
          type: {name: 'Тип', id: 0}
        }
      }
    },
    watch: {
      cardURL: function (val) {
        this.resultCard.imgur_url = (val === '') ? 'https://i.imgur.com/ZRguE01.png' : val
      },
      cardTitle: function (val) {
        this.resultCard.title = val
      },
      'cardType.value': function (val) {
        this.resultCard.type = (typeof val === 'string') ? {name: val, id: 0} : val
      },
      'cardAuthor.value': function (val) {
        this.resultCard.author = (typeof val === 'string') ? {name: val, id: 0} : val
      },
      'cardRate.value': function (val) {
        const rates = {
          0: 'Без сознания',
          1: 'Одним глазком',
          2: 'Годнота',
          3: 'Абсолютная годнота',
          9: 'Скрытая годнота'
        }
        this.resultCard.rating = (typeof val === 'string') ? {name: rates[val], rate: val} : val
      },
      'cardTags.value': function (val) {
        let tags = []
        if (Array.isArray(val)) {
          for (let i = 0; i < val.length; i++) {
            const tag = val[i]
            if (typeof tag === 'string') {
              tags.push({name: tag, id: 0})
            } else {
              tags.push(tag)
            }
          }
          this.resultCard.tags = tags
        } else {
          this.resultCard.tags = {name: val, id: 0}
        }
      }
    },
    methods: {
      openDialog () {
        this.getFilters()
        this.createCardDialog = true
      },
      getFilters () {
        let pageId = this.$store.getters.HOME_PAGE
        axios.get(urlFilters, {
          params: {
            page_id: pageId
          }
        })
          .then(response => {
            let data = response.data
            this.cardType.variants = data.types
            this.cardTags.variants = data.tags
            this.cardRate.variants = data.rates
            this.cardAuthor.variants = data.authors
          })
          .catch(error => {
            console.log(error)
          })
        if (this.cardInfo) {
          this.cardType.value = this.cardInfo.type
          this.cardTags.value = this.cardInfo.tags
          this.cardRate.value = this.cardInfo.rating.rate
          this.cardAuthor.value = (this.cardInfo.author === '') ? this.cardInfo.author : null
          this.cardTitle = this.cardInfo.title
          this.cardURL = this.cardInfo.imgur_url
          this.resultCard.color = this.cardInfo.color
          this.cardId = this.cardInfo.id
        }
      },
      post () {
        if (this.$refs.form.validate()) {
          this.loading = true
          let r = new Promise((resolve, reject) => {
            axios.post(urlPost, {
              title: this.cardTitle,
              author: this.cardAuthor.value,
              img_url: this.cardURL,
              rating: this.cardRate.value,
              tags: this.cardTags.value,
              type: this.cardType.value,
              id: this.cardId
            })
              .then(response => {
                if (response.status === 200) {
                  resolve(true)
                } else {
                  reject(new Error('Creating card error'))
                }
              })
              .catch(error => {
                console.log(error)
                reject(new Error('Creating card error'))
              })
          })
          r.then(
            (result) => {
              this.loading = false
              this.success = true
              this.color = 'success'
              let self = this
              setTimeout(function () {
                self.$router.go()
              }, 300)
            },
            (error) => {
              console.log(error)
              this.loading = false
              this.success = false
              this.color = 'red lighten-1'
            }
          )
        }
      }
    }
  }
</script>

<style scoped>
  div .v-dialog {
    z-index: 999
  }

  .headline {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    margin-bottom: 1.3rem;
  }

  .create-card {
    padding: 1rem 3rem;
    min-width: 68rem;
  }

  .card-form {
    padding-right: 1.3rem;
  }

  .card-wrapper {
    min-width: 33rem;
    padding-left: 1.3rem;
    display: block;
  }

  @media screen and (max-width: 600px) {
    .card-wrapper {
      min-width: 25rem;
    }
  }

  @media screen and (max-width: 1000px) {
    .card-wrapper {
      padding-left: 0;
    }
    .create-card {
      min-width: 0;
    }
  }
</style>



