<template>
  <div id="content">

    <div id="sidebar" :class="{ invisible: !isMenuVisible }">
        <div class="filter-wrapper">
          <div class="filter-group">
            <v-select
              :items="types"
              v-model="typesPicked"
              item-text="name"
              item-value="id"
              label="Что искать?"
              attach
              chips
              deletable-chips
              dense
              multiple
            ></v-select>
          </div>
          <div class="filter-group">
            <v-select
              :items="authors"
              v-model="authorsPicked"
              item-text="name"
              item-value="id"
              label="Кого-то конкретного?"
              attach
              chips
              deletable-chips
              dense
              multiple
            ></v-select>
          </div>
          <div class="filter-group">
            <v-select
              :items="tags"
              v-model="tagsPicked"
              item-text="name"
              item-value="id"
              label="Еще точнее?"
              attach
              chips
              deletable-chips
              dense
              multiple
            ></v-select>
          </div>
          <div class="filter-group">
            <v-select
              :items="rates"
              v-model="ratesPicked"
              item-text="name"
              item-value="rate"
              label="Тонкая настройка"
              attach
              chips
              deletable-chips
              dense
              multiple
            ></v-select>
          </div>
          <div class="filter-group">
            <v-btn @click="applyFilters" class="ml-3" outlined color="teal darken-2">
              <v-icon left>mdi-filter-variant</v-icon> Применить
            </v-btn>
          </div>
        </div>
        <v-spacer></v-spacer>
        <owner-card></owner-card>
    </div>

    <div class="sidebar-close-layer"
    :class="{ invisible: !isMenuVisible }"
    @click.prevent="toggle"></div>
    <div id="burger"
        :class="{ active: isMenuVisible }"
        @click.prevent="toggle">
      <button type="button" class="burger-button" title="Menu">
        <span class="burger-bar burger-bar--1"></span>
        <span class="burger-bar burger-bar--2"></span>
        <span class="burger-bar burger-bar--3"></span>
      </button>
    </div>

    <div class="container-wrapper">
      <div class="header-space"></div>
      <v-container class="content-wrapper">
        <v-layout wrap>
          <v-flex xs12 md12 lg6 xl4 v-for="item in info" :key="item.id">
            <card
              :editing="false"
              :info="item"
              :tagsFiltered="tagsFiltered">
            </card>
          </v-flex>
        </v-layout>
      </v-container>
    </div>

    <infinite-loading
    v-if="!errored"
    :identifier="infiniteId"
    @infinite="infiniteHandler"
    spinner="spiral">
      <div slot="no-more"></div>
      <div slot="no-results"></div>
    </infinite-loading>
    <v-snackbar
      v-model="errored"
      bottom
      color="red lighten-2"
    >
      Что-то пошло не так...
    </v-snackbar>

  </div>
</template>

<script>
  import Vuetify from 'vuetify'
  import OwnerCard from './OwnerCard'
  import axios from 'axios'
  import { mapGetters } from 'vuex'

  const urlCards = '/api/v1/cards/'
  const urlFilters = '/api/v1/filters/'

  export default {
    name: 'Content',
    vuetify: new Vuetify(),
    components: { Card: () => import('./Card.vue'), OwnerCard },
    data () {
      return {
        info: [],
        next_page: urlCards + '?page=1',
        errored: false,
        isMenuVisible: false,
        typesPicked: null,
        types: [],
        authorsPicked: null,
        authors: [],
        tagsPicked: [],
        tags: [],
        tagsFiltered: [],
        ratesPicked: null,
        rates: [],
        infiniteId: 0,
        params: {},
        routePage: this.$route.params.page
      }
    },
    methods: {
      clearAll () {
        this.info = []
        this.next_page = urlCards + '?page=1'
        this.errored = false
        this.isMenuVisible = false
        this.typesPicked = null
        this.types = []
        this.authorsPicked = null
        this.authors = []
        this.tagsPicked = []
        this.tags = []
        this.tagsFiltered = []
        this.ratesPicked = null
        this.rates = []
        this.infiniteId = 0
        this.params = {}
      },
      applyFilters () {
        this.info = []
        this.params = {
          author: this.authorsPicked + '',
          type: this.typesPicked + '',
          tags: this.tagsPicked + '',
          rating: this.ratesPicked + ''
        }
        this.tagsFiltered = Array.from(this.tagsPicked)
        this.next_page = urlCards + '?page=1'
        this.infiniteId += 1
      },
      infiniteHandler ($state) {
        this.params['page_id'] = this.routePage
        axios.get(this.next_page, {
          params: this.params
        })
          .then(response => {
            this.info = this.info.concat(response.data.results)
            this.next_page = response.data.next
            if (this.next_page == null) {
              $state.complete()
            } else {
              $state.loaded()
            }
          })
          .catch(error => {
            console.log(error)
            this.errored = true
          })
      },
      getFilters () {
        axios.get(urlFilters, {
          params: {
            page_id: this.routePage
          }
        })
          .then(response => {
            let data = response.data
            this.types = data.types
            this.tags = data.tags
            this.rates = data.rates
            this.authors = data.authors
          })
          .catch(error => {
            console.log(error)
          })
      },
      toggle () {
        this.isMenuVisible = !this.isMenuVisible
      }
    },
    computed: {
      ...mapGetters({
        isAuth: 'IS_AUTH'
      })
    },
    watch: {
      'isAuth': function (newVal, oldVal) {
        this.info = []
        this.next_page = urlCards + '?page=1'
        this.infiniteId += 1
      }
    },
    beforeRouteUpdate (to, from, next) {
      this.routePage = to.params.page
      this.clearAll()
      this.getFilters()
      next()
    },
    created () {
      this.getFilters()
    }
  }
</script>

<style scoped>
  @import url('https://fonts.googleapis.com/css?family=Montserrat:300,400&display=swap');

  .container-wrapper {
    float: right;
    width: 80%;
  }

  .content-wrapper {
    width: 100%;
  }

  @media screen and (max-width: 1000px) {
    .container-wrapper {
      float: right;
      width: 60%;
    }
    div#sidebar {
      width: 40%;
    }
  }

  @media screen and (max-width: 600px) {
    .container-wrapper {
      width: 100%;
    }

    div#sidebar {
      width: 21rem;
    }

    div.sidebar-close-layer {
      display: block;
    }

    #sidebar.invisible {
      left: -21rem;
    }

    .sidebar-close-layer.invisible {
      display: none;
    }
  }

  .header-space {
    height: 3rem;
  }

  .row {
    margin: 0;
  }

  /* Sidebar */
  #sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 20%;
    z-index: 111;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    background: #FAFAFA;
    box-shadow: inset -1px 0px #00000030;
    transition: 0.3s all ease-out;
  }

  #sidebar::-webkit-scrollbar {
    display: none;
  }

  .sidebar-close-layer {
    display: none;
    position: fixed;
    right: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 3;
  }

  .filter-wrapper {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .filter-group {
    margin-bottom: 1rem;
  }

  .filter-name {
    text-align: left;
    margin-left: 1rem;
    color: #00000070;
  }

  .v-select {
    margin-left: 7%;
    width: 85%;
  }

  /* Menu toogle */
  .burger-button:focus {
    outline: 0;
  }

  .burger-button {
    cursor: pointer;
    position: relative;
    height: 30px;
    width: 32px;
    display: block;
    z-index: 999;
    border: 0;
    border-radius: 0;
    background-color: transparent;
    pointer-events: all;
    transition: transform .6s cubic-bezier(.165,.84,.44,1);
  }

  .burger-bar {
    background-color: #00000090;
    position: absolute;
    top: 50%;
    right: 6px;
    left: 6px;
    height: 2px;
    width: auto;
    margin-top: -1px;
    transition: transform .6s cubic-bezier(.165,.84,.44,1),
      opacity .3s cubic-bezier(.165,.84,.44,1),
      background-color .6s cubic-bezier(.165,.84,.44,1);
  }

  .burger-bar--1 {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
  }

  .burger-bar--2 {
    transform-origin: 100% 50%;
    transform: scaleX(.8);
  }

  .burger-button:hover .burger-bar--2 {
    transform: scaleX(1);
  }

  .no-touchevents .burger-bar--2:hover {
    transform: scaleX(1);
  }

  .burger-bar--3 {
    transform: translateY(6px);
  }

  #burger.active .burger-button {
    transform: rotate(-180deg);
  }

  #burger.active .burger-bar--1 {
    transform: rotate(45deg)
  }

  #burger.active .burger-bar--2 {
    opacity: 0;
  }

  #burger.active .burger-bar--3 {
    transform: rotate(-45deg)
  }

  #burger {
    display: none;
  }

  @media screen and (max-width: 600px) {
    #burger {
      display: block;
      position: fixed;
      z-index: 1000;
      top: 0.9rem;
      left: 0.9rem;
    }
  }
</style>



