<template>
  <div class="card">
    <v-hover v-slot:default="{ hover }">
    <v-card
      tile
      outlined
      :color="info.color + '10'">

      <v-layout row wrap>
        <v-flex xs12 sm5 class="image-wrapper">
          <v-img
            :src="info.imgur_url || 'https://i.imgur.com/ZRguE01.png'"
            @error="imageLoadError"
            aspect-ratio="1"
            height="17rem"
            contain
          >
            <template v-slot:placeholder>
              <div class="load-img-wrapper">
                <v-progress-circular
                  indeterminate
                  :color="info.color + ' lighten-5'"
                  style="margin: auto"></v-progress-circular>
              </div>
            </template>
          </v-img>
        </v-flex>
        <v-flex xs12 sm7>
          <div class="text-wrapper">
            <span :type="info.type.id" class="type">{{info.type.name.toLowerCase()}}<span v-if="info.author != null" :author="info.author.id"> • {{info.author.name}}</span></span>
            <v-icon
              :style="hover ? 'display: block' : 'display: none'"
              class="delete-icon"
              color="grey"
              v-if="is_home && !editing"
              @click="deleteDialog = true">
              mdi-delete
            </v-icon>
            <create-card v-if="is_home && !editing" :cardInfo="info" :editing="true">
              <v-icon
                :style="hover ? 'display: block' : 'display: none'"
                class="edit-icon"
                color="grey">
                mdi-pencil
              </v-icon>
            </create-card>
            <h1>{{info.title}}</h1>
            <p :rating="info.rating.rate" class="rating">{{info.rating.name}}</p>
            <div class="tags-wrapper">
              <div class="tag" v-for="item in info.tags" :key="item.id" :class="{activetag: tagsFiltered.includes(item.id)}">
                {{item.name}}
              </div>
            </div>
          </div>
        </v-flex>
      </v-layout>
    </v-card>
    </v-hover>

    <v-dialog
      v-model="deleteDialog"
    >
      <v-card class="delete-card">
        <h1>Удалить <span>{{info.title}}</span>?</h1>
        <v-card-actions>
          <v-spacer></v-spacer>
            <v-btn small color="error" @click="deleteEntry">
              <v-progress-circular
                v-if="loading"
                indeterminate
                color="white darken-1"
                class="mr-1"
                size="18"
                width="1"
              ></v-progress-circular>
              <v-icon left v-if="!loading && success == null">mdi-delete</v-icon>
              <v-icon left v-if="!loading && success == true">mdi-check</v-icon>
              <v-icon left v-if="!loading && success == false">mdi-alert-circle-outline</v-icon>
              Удалить
            </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from 'axios'

  const urlDelete = '/api/v1/card/delete/'

  export default {
    name: 'Card',
    props: ['info', 'tagsFiltered', 'editing'],
    data () {
      return {
        is_home: false,
        deleteDialog: false,
        editDialog: false,
        loading: false,
        success: null
      }
    },
    methods: {
      imageLoadError () {
        this.info.imgur_url = 'https://i.imgur.com/ZRguE01.png'
      },
      deleteEntry () {
        if (this.info.id !== 0) {
          this.loading = true
          let r = new Promise((resolve, reject) => {
            axios.post(urlDelete, {
              id: this.info.id
            })
              .then(response => {
                if (response.status === 200) {
                  resolve(true)
                } else {
                  reject(new Error('Deleting card error'))
                }
              })
              .catch(error => {
                console.log(error)
                reject(new Error('Deleting card error'))
              })
          })
          r.then(
            (result) => {
              this.loading = false
              this.success = true
              let self = this
              setTimeout(function () {
                self.$router.go()
              }, 300)
            },
            (error) => {
              console.log(error)
              this.loading = false
              this.success = false
            }
          )
        }
      }
    },
    created () {
      if (this.$store.getters.HOME_PAGE === this.$route.params.page) {
        this.is_home = true
      }
    }
  }
// author: {id: 1, name: "Тарантино"}
// color: "#e1270e"
// id: 19
// imgur_url: "https://i.imgur.com/1TuALiL.png"
// rating: {rate: 2, name: "Годнота"}
// tags: [{id: 6, name: "Бодрячком"}, {id: 7, name: "Среднесть существа"}]
// title: "Бешеные псы"
// type: {id: 1, name: "Фильм"}
</script>

<style scoped>
  @import url('https://fonts.googleapis.com/css?family=Oswald:200,300&display=swap');


  .card {
    font-family: 'Montserrat', sans-serif;
    position: relative;
  }

  div.card .v-card {
    overflow: hidden;
    border: none;
  }

  .delete-icon {
    position: absolute;
    top: 1rem;
    right: 1rem;
    cursor: pointer;
  }

  .edit-icon {
    position: absolute;
    top: 1rem;
    right: 3rem;
    cursor: pointer;
  }

  .image-wrapper {
    margin: 1rem 0;
  }

  .load-img-wrapper {
    display: flex;
    justify-content: center;
    height: 17rem;
    width: 100%;
  }

  .text-wrapper {
    width: 95%;
    height: 100%;
    margin-left: 0.3rem;
    margin-right: 5%;
    margin-top: 0.7rem;
    text-align: left;
    color: #000000;
  }

  h1 {
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
    margin-top: 0
  }

  span, p {
    font-weight: 400;
  }

  span.type {
    color: #a7a7a7;
  }

  span.author {
    font-size: 0.9rem;
    font-family: 'Roboto', sans-serif;
    color: #a7a7a7;
  }

  p.rating {
    margin-top: 1.3rem;
  }

  .tags-wrapper {
    margin-top: 5.1rem;
  }

  .tag {
    font-size: 0.9rem;
    display: inline-block;
    padding: 0.1rem 0.5rem;
    border-radius: 0.3rem;
    background: #e2e2e2;
    margin-right: 0.3rem;
    margin-bottom: 0.3rem;
    opacity: 0.5;
  }

  .tag.activetag {
    background: #c5c5c5;
    color: #000000;
    font-weight: 600;
  }

  @media screen and (max-width: 600px) {
    .text-wrapper {
      margin-bottom: 3rem;
      text-align: center;
      margin-left: 0;
    }
  }

  .delete-card h1 {
    padding: 1rem 3rem;
    font-size: 1.3rem;
  }

  .delete-card span {
    font-weight: 600
  }
</style>



